import { useState, useEffect } from "react";
import CategoryFilter from "./CategoryFilter";

const categories = [
  {
    name: "",
    value: "all",
  },
  {
    name: "",
    value: "category1",
  },
  {
    name: "",
    value: "category2",
  },
  {
    name: "",
    value: "category3",
  },
];
const qnaList = [
  {
    category: "category1",
    question: "+ ?",
    answer:
      "+ , , , , , Star      .                .",
  },
  {
    category: "category2",
    question: "+     ?",
    answer:
      "+     .    /     /      /         /       /   /,   ",
  },
  {
    category: "category3",
    question: "+    ?",
    answer:
      "+   ,  ,  , ,  TV   .       .",
  },
  {
    category: "category1",
    question: " ?",
    answer: "  ",
  },
  {
    category: "category2",
    question: "",
    answer: "  ",
  },
  {
    category: "category3",
    question: "+ ?",
    answer: " ",
  },

  {
    category: "category1",
    question: "+ ?",
    answer:
      "+ , , , , , Star      .                .",
  },
  {
    category: "category2",
    question: "",
    answer: " ",
  },
  {
    category: "category3",
    question: "+     ?",
    answer: "",
  },
];

const Section3 = () => {
  const [ca, Setca] = useState("all");
  const [content, Setcontent] = useState(qnaList);
  const [tabT, settabT] = useState(0);
  const [Toggle, setToggle] = useState(0);
  const [swc, setSwc] = useState(false);

  //console.log(categories[0].value);
  // console.log(qnaList[0].category);
  useEffect(() => {
    Setcontent(
      qnaList.filter((item) => {
        if (ca === "all") return true;
        if (ca === item.category) return true;
      })
    );
  }, [ca]);

  // useEffect(() => {
  //   setSwc(true);
  // }, [tabT]);
  return (
    <section className="Section3 inner">
      <div className="title">
        <h2>  </h2>
        <a href="">
           <i className="xi-arrow-right"></i>
        </a>
      </div>
      <div className="content">
        <div className="tab_bt">
          <ul>
            {categories.map((ca, idx) => (
              <li
                key={idx}
                onClick={() => {
                  Setca(ca.value);
                  setToggle(0);
                  setSwc(false);
                  settabT(idx);
                }}
                className={`${tabT === idx ? "on" : ""}`}
              >
                {ca.name}
              </li>
            ))}
          </ul>
        </div>
        <div className="tab_content">
          {content.map((item, idx) => {
            const active = idx === Toggle && swc ? "on" : "";
            return (
              <div
                onClick={() => {
                  setToggle(idx);
                  if (swc) {
                    if (active) {
                      setSwc(!swc);
                    }
                  } else {
                    setSwc(!swc);
                  }
                }}
                className={`tab_box ${active}`}
                key={idx}
              >
                <h3>{item.question}</h3>
                <div>{item.answer}</div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default Section3;
